body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}


.result-display{
	display: table-cell;
	height: 100px;
	width: 400px;
	border: 3px solid black;
	text-align: right;
	vertical-align: bottom;
	background: black;
}
.result{
	font-size: 3em;
	font-family: 'Roboto', sans-serif;
	margin-right: 5px;
	color: #fff;
}

.all-enter {
	font-size: 1.2em;
	font-family: 'Roboto', sans-serif;
	margin-right: 5px;
	color: #fff;
}

.container{
	width: 297px;
	height: 400px;
	top: 200px;
}
.num{
	width: 99px;
	display: block;
	background: #666;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	color: #fff;
	float: left;
}
.zero{
	width: 198px;
	display: block;
	background: #666;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	color: #fff;
	float: left;
}
.decimal{
	width: 99px;
	display: block;
	background: #666;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	color: #fff;
	position: relative;
	top: -100px;
	left: 198px;
}
.op-box{
	width: 99px;
	height: 400px;
	float: right;
	top: -500px;
	position: relative;
}
.operators {
	display: block;
	background: #888;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	width: 99px;
	color: #fff;
	float: right;
}

.equal{
	display: block;
	position: relative;
	background: #888;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	width: 198px;
	color: #fff;
}
.reset{
	display: block;
	background: red;
	border: 1px solid black;
	font-family: 'Lato', sans-serif;
	font-size: 2em;
	height: 100px;
	width: 198px;
	color: #fff;
	float: left;
}


.calculator {
	position: relative;
	border: 2px solid black;
	width: 400px;
	height: 603px;
	margin: 40px auto;
}
.box{
	height: 100px;
	width: 400px;
}
